.row {
  color: white;
  margin-left: 20px;
}

.row__posters {
  display: flex;
  overflow-x: scroll;
  overflow-y: hidden;
  padding: 20px;
}

.row__posters::-webkit-scrollbar {
  display: none;
}

.row__poster {
  cursor: pointer;
  max-height: 150px;
  object-fit: contain;
  margin-right: 15px;
  width: 100%;
  transition: transform 450ms;
}

.row__poster:hover {
  transform: scale(1.08);
  opacity: 1;
}

.row__posterLarge {
  max-height: 270px;
}

.row__posterLarge:hover {
  transform: scale(1.09);
  opacity: 1;
}

.row__videoContainer {
  width: 100%;
  height: 500px;
  position: relative;
  overflow: hidden !important;
}

.row__video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  outline: none;
}
