.loginScreen {
  position: relative;
  height: 100%;
  background: url('../../assets/loginbg.jpg');
  overflow-x: hidden;
}

.loginScreen__gradient {
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.5);
  background-image: linear-gradient(
    to top,
    rgba(0, 0, 0, 0.8) 0%,
    rgba(0, 0, 0, 0) 60%,
    rgba(0, 0, 0, 0.8) 100%
  );
}

.loginScreen__logo {
  position: fixed;
  left: 0;
  width: 150px;
  object-fit: contain;
  padding-left: 20px;
}

.loginScreen__body {
  z-index: 1;
  color: #fff;
  padding: 20px;
  position: absolute;
  top: 30%;
  left: 50%;
  transform: translateX(-50%);
  text-align: center;
  width: 100%;
}

.loginScreen__body > h1 {
  font-size: 3.125rem;
  margin-bottom: 20px;
}

.loginScreen__body > h2 {
  font-size: 2rem;
  font-weight: 400;
  margin-bottom: 30px;
}

.loginScreen__body > h3 {
  font-size: 1.2rem;
  font-weight: 400;
}

.loginScreen__button {
  position: fixed;
  top: 20px;
  right: 20px;
  padding: 10px 20px;
  font-size: 1rem;
  color: white;
  background: #e50914;
  font-weight: 600;
  cursor: pointer;
  border: none !important;
  outline: none;
}

.loginScreen__input {
  margin: 20px;
}

.loginScreen__input > form > input {
  padding: 10px;
  outline-width: 0 !important;
  outline-width: none;
  height: 30px;
  width: 30%;
  border: none;
  max-width: 600px;
}

.loginScreen__input > form {
  display: flex;
  align-items: center;
  justify-content: center;
}

.loginScreen__getStarted {
  padding: 16px 20px;
  border: none;
  outline-width: 0;
  font-size: 1rem;
  color: white;
  height: 50px;
  background-color: #e50914;
}
