.profileScreen {
  height: 100vh;
  color: white;
}

.proflieScreen__body {
  display: flex;
  padding-top: 8%;
  width: 50%;
  margin-left: auto;
  margin-right: auto;
  flex-direction: column;
  max-width: 800px;
}

.profileScreen__info {
  display: flex;
}

.profileScreen__info > img {
  height: 100px;
  object-fit: contain;
}

.proflieScreen__body > h1 {
  font-size: 60px;
  font-weight: 400;
  border-bottom: 1.5px solid #282c2d;
  margin-bottom: 20px;
}

.profileScreen__details {
  flex: 1;
  margin-left: 25px;
  color: white;
}

.profileScreen__plans {
  margin-top: 20px;
}

.profileScreen__plans > h3 {
  padding-bottom: 10px;
  border-bottom: 1px solid #282c2d;
}

.profileScreen__plans > p {
  margin-top: 10px;
}

.profileScreen__detail {
  margin-top: 20px;
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-between;
}

.profileScreen__detail:hover {
  opacity: 0.8;
}

.current {
  background-color: #808080 !important;
  cursor: unset !important;
}

.profileScreen__detail > button {
  padding: 12px 17px;
  border: none;
  outline: none;
  color: white;
  background: #e50914;
  cursor: pointer;
  font-size: 0.8rem;
  font-weight: 400;
  cursor: pointer;
}

.profileScreen__detail > h4 {
  font-weight: 600;
  padding-left: 30px;
}

.profileScreen__detail > h4 > span {
  font-size: 12px;
}

.profileScreen__details > h2 {
  padding: 15px;
  background: #808080;
  font-size: 15px;
  padding-left: 20px;
}

.profileScreen__signOut {
  padding: 15px 20px;
  border: none;
  outline: none;
  margin-top: 5%;
  color: white;
  background: #e50914;
  cursor: pointer;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  width: 100%;
}
