.signupScreen {
  max-width: 300px;
  padding: 50px;
  background: rgba(0, 0, 0, 0.85);
  margin: 0 auto;
}

.signupScreen > form {
  display: flex;
  flex-direction: column;
}

.signupScreen > form > input {
  height: 40px;
  padding: 5px 15px;
  border: none;
  margin-bottom: 14px;
  border-radius: 5px;
  outline-width: 0;
}

.signupScreen > form > button {
  padding: 16px 20px;
  background: #e50914;
  border: none;
  color: #fff;
  font-size: 1rem;
  font-weight: 600;
  border-radius: 5px;
  outline: none;
  cursor: pointer;
  margin-top: 20px;
}

.signupScreen > form > h1 {
  margin-bottom: 20px;
  text-align: left;
}

.signupScreen > form > h4 {
  margin-top: 20px;
  text-align: left;
}

.signupScreen__gray {
  color: gray;
  margin-right: 10px;
}

.signupScreen__link:hover {
  text-decoration: underline;
  cursor: pointer;
}
